"use client";

import { useState, useEffect } from 'react';
import { VerseCard } from '@/components/verse-card';
import { MobileNavigation } from '@/components/mobile-navigation';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { verseService } from '@/lib/verse-service';
import { Verse } from '@/lib/types';

export default function SearchPage() {
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState<Verse[]>([]);
  const [isSearching, setIsSearching] = useState(false);
  const [hasSearched, setHasSearched] = useState(false);
  const [topics, setTopics] = useState<string[]>([]);

  useEffect(() => {
    // Load available topics
    const availableTopics = verseService.getTopics();
    setTopics(availableTopics);
  }, []);

  const handleSearch = async (query: string = searchQuery) => {
    if (!query.trim()) return;

    setIsSearching(true);
    setHasSearched(true);

    // Add a small delay for better UX
    setTimeout(() => {
      try {
        const results = verseService.searchVerses(query.trim());
        setSearchResults(results);
      } catch (error) {
        console.error('Search failed:', error);
        setSearchResults([]);
      } finally {
        setIsSearching(false);
      }
    }, 300);
  };

  const handleTopicClick = (topic: string) => {
    setSearchQuery(topic);
    handleSearch(topic);
  };

  const clearSearch = () => {
    setSearchQuery('');
    setSearchResults([]);
    setHasSearched(false);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  };

  return (
    <div className="min-h-screen pb-20">
      {/* Header */}
      <header className="bg-white/80 dark:bg-slate-900/80 backdrop-blur-lg border-b border-amber-200 dark:border-slate-700 sticky top-0 z-40">
        <div className="max-w-md mx-auto px-4 py-4">
          <div className="text-center">
            <h1 className="text-2xl font-bold text-amber-800 dark:text-amber-200 mb-1">
              Search
            </h1>
            <p className="text-sm text-slate-600 dark:text-slate-400">
              Find verses by keyword, topic, or reference
            </p>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-md mx-auto px-4 py-6">
        {/* Search Bar */}
        <div className="mb-6">
          <div className="relative">
            <svg className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
            <Input
              type="text"
              placeholder="Search verses, topics, or references..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyPress={handleKeyPress}
              className="pl-10 pr-20 bg-white/70 dark:bg-slate-800/70 backdrop-blur-sm border-amber-200 dark:border-slate-600 text-base"
            />
            {searchQuery && (
              <button
                onClick={clearSearch}
                className="absolute right-12 top-1/2 transform -translate-y-1/2 text-slate-400 hover:text-slate-600 dark:hover:text-slate-300"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            )}
          </div>
          <Button
            onClick={() => handleSearch()}
            disabled={!searchQuery.trim() || isSearching}
            className="w-full mt-3 bg-amber-500 hover:bg-amber-600 text-white font-medium"
          >
            {isSearching ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                Searching...
              </>
            ) : (
              'Search Verses'
            )}
          </Button>
        </div>

        {/* Popular Topics */}
        {!hasSearched && (
          <div className="mb-8">
            <h3 className="font-semibold text-slate-800 dark:text-slate-200 mb-4">
              Popular Topics
            </h3>
            <div className="flex flex-wrap gap-2">
              {topics.map((topic) => (
                <button
                  key={topic}
                  onClick={() => handleTopicClick(topic)}
                  className="bg-amber-100 dark:bg-amber-900/30 text-amber-700 dark:text-amber-400 px-3 py-2 rounded-full text-sm font-medium capitalize hover:bg-amber-200 dark:hover:bg-amber-900/50 transition-colors duration-200"
                >
                  {topic}
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Search Examples */}
        {!hasSearched && (
          <div className="mb-8">
            <h3 className="font-semibold text-slate-800 dark:text-slate-200 mb-4">
              Search Examples
            </h3>
            <div className="space-y-2">
              {[
                { query: 'love', description: 'Find verses about love' },
                { query: 'John 3:16', description: 'Search by reference' },
                { query: 'strength', description: 'Verses about strength' },
                { query: 'peace', description: 'Find peaceful verses' }
              ].map((example) => (
                <button
                  key={example.query}
                  onClick={() => handleTopicClick(example.query)}
                  className="w-full text-left bg-white/70 dark:bg-slate-800/70 backdrop-blur-sm border border-amber-200 dark:border-slate-600 rounded-lg p-3 hover:bg-amber-50 dark:hover:bg-slate-700 transition-colors duration-200"
                >
                  <div className="font-medium text-slate-800 dark:text-slate-200 text-sm">
                    "{example.query}"
                  </div>
                  <div className="text-xs text-slate-600 dark:text-slate-400 mt-1">
                    {example.description}
                  </div>
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Search Results */}
        {hasSearched && (
          <div className="mb-6">
            <div className="flex items-center justify-between mb-4">
              <h3 className="font-semibold text-slate-800 dark:text-slate-200">
                Search Results
              </h3>
              <span className="text-sm text-slate-600 dark:text-slate-400">
                {searchResults.length} verse{searchResults.length !== 1 ? 's' : ''} found
              </span>
            </div>

            {searchResults.length > 0 ? (
              <div className="space-y-6">
                {searchResults.map((verse, index) => (
                  <VerseCard
                    key={`${verse.reference}-${index}`}
                    verse={verse}
                    showReflection={true}
                    showFavoriteButton={true}
                  />
                ))}
              </div>
            ) : (
              /* No Results */
              <div className="text-center py-12">
                <div className="text-slate-400 mb-4">
                  <svg className="w-16 h-16 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                  </svg>
                </div>
                <h3 className="text-lg font-medium text-slate-600 dark:text-slate-400 mb-2">
                  No verses found
                </h3>
                <p className="text-sm text-slate-500 dark:text-slate-500 mb-6">
                  Try searching for different keywords, topics, or Bible references.
                </p>
                <div className="space-y-2">
                  <Button
                    variant="outline"
                    onClick={clearSearch}
                    className="text-amber-600 border-amber-200 hover:bg-amber-50 dark:text-amber-400 dark:border-amber-800 dark:hover:bg-amber-900/20"
                  >
                    Try New Search
                  </Button>
                </div>
              </div>
            )}
          </div>
        )}

        {/* Search Tips */}
        {hasSearched && searchResults.length > 0 && (
          <div className="bg-white/70 dark:bg-slate-800/70 backdrop-blur-sm border border-amber-200 dark:border-slate-600 rounded-lg p-4">
            <h4 className="font-semibold text-slate-800 dark:text-slate-200 mb-2 text-sm">
              ðŸ’¡ Search Tips
            </h4>
            <ul className="text-xs text-slate-600 dark:text-slate-400 space-y-1">
              <li>â€¢ Use specific keywords for better results</li>
              <li>â€¢ Try searching by Bible book (e.g., "John", "Psalms")</li>
              <li>â€¢ Search by topic (e.g., "love", "hope", "faith")</li>
              <li>â€¢ Use exact references (e.g., "John 3:16")</li>
            </ul>
          </div>
        )}
      </main>

      <MobileNavigation />
    </div>
  );
}
